import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Portfolio from '@/components/Portfolio'

Vue.use(Router)

export default new Router({
routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      meta: { transitionSlidePage: 'slide'}
    },
    {
      path: '/Portfolio',
      name: 'Portfolio',
      component: Portfolio,
      meta: { transitionSlidePage: 'slide' }
    }
  ]
})



